// Colors
// ---------------------------------

// Default

$color-default-blue:                #102654;
$color-default-white:               #ffffff;
$color-default-white-opacity:       #ffffff70;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

// $color-neon-blue:                   #2c39f2;
$color-dark-black:                  #030a1a;
$color-neon-blue:                   #2e35ac;
$color-light-blue:                  #103aae;
$color-dark-blue:                   #00359f;
$color-darken-blue:                 #1c3374;
$color-soft-blue:                   rgba(16, 38, 84, 0.50);
$color-torch-red:                   #d10a42;
$color-shine-red:                   #ed0233;
$color-soft-grey:                   rgba(120, 120, 120, 0.20);
$color-background-grey:             #f8f9fc;
$color-text-grey:                   #e8e8e8;
// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

//    html   
$fz-default:                        16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-placeholder:                    "Roboto Condensed", "Arial", sans-serif;
$ff-title:                           "Alumni Sans", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

//      $tf-default,     
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-768:                            768px;
$vp-1366:                           1366px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
